/* harmony default export */ var Util = ({

  //
  checkUsername: function checkUsername(username) {

    //
    if (username === '' || username === null) {
      return {
        usernameStatus: 2,
        usernameNotice: '',
        flag: false
      };
    }

    //
    if (username.length < 3 || username.length > 15) {
      return {
        usernameStatus: 2,
        usernameNotice: '3~15',
        flag: false
      };
    }

    //
    if (!/^[A-Za-z]/.test(username)) {
      return {
        usernameStatus: 2,
        usernameNotice: '',
        flag: false
      };
    }

    //
    if (!/^[a-zA-Z0-9_]*$/.test(username)) {
      return {
        usernameStatus: 2,
        usernameNotice: '',
        flag: false
      };
    }

    //
    return {
      usernameStatus: 1,
      usernameNotice: '',
      flag: true
    };
  },

  //
  checkPassword: function checkPassword(password) {

    //
    if (password === null || password === '') {
      return {
        passwordStatus: 2,
        passwordNotice: '',
        flag: false
      };
    }

    //
    if (password.length < 6 || password.length > 16) {
      return {
        passwordStatus: 2,
        passwordNotice: '6~16',
        flag: false
      };
    }

    //
    return {
      passwordStatus: 1,
      passwordNotice: '',
      flag: true
    };
  },

  //
  checkSecondPassword: function checkSecondPassword(password, secondPassword) {

    //
    if (secondPassword === null || secondPassword === '') {
      return {
        secondPasswordStatus: 2,
        secondPasswordNotice: '',
        flag: false
      };
    }

    //
    if (password !== secondPassword) {
      return {
        secondPasswordStatus: 2,
        secondPasswordNotice: '',
        flag: false
      };
    }

    //
    return {
      secondPasswordStatus: 1,
      secondPasswordNotice: '',
      flag: true
    };
  },

  //
  checkEmail: function checkEmail(email) {

    //
    if (email === null || email === '') {
      return {
        emailStatus: 2,
        emailNotice: '',
        flag: false
      };
    }

    var reg = /^\w+([_\.\-]\w+)*@\w+([_\-\.]\w+)*\.\w+([_\.]\w+)*$/;

    //
    if (!reg.test(email)) {
      return {
        emailStatus: 2,
        emailNotice: '',
        flag: false
      };
    }

    //
    return {
      emailStatus: 1,
      emailNotice: '',
      flag: true
    };
  },

  //
  checkAccept: function checkAccept(isAccept) {

    //
    if (!isAccept) {
      return {
        acceptStatus: 2,
        acceptNotice: '',
        flag: false
      };

      //
    } else {
      return {
        acceptStatus: 1,
        acceptNotice: '',
        flag: true
      };
    }
  }
});
