
<!--  -->

<template>
  <section class="receive-info inner-c" @click="recoverStatus()">
    <div class="title"></div>
    <transition name="fade">
      <div class="show-result" v-if="isShowResult"></div>
    </transition>
    <div class="receive-content">
      <form>
        <div class="row place clearfix">
          <div class="content clearfix">
            <span class="xing">*</span>
            <span class="name"></span>
          </div>
          <!--  s -->
          <selectAddress></selectAddress>
          <!--  e -->
          <span class="right-icon" v-if="noticeStatus1 == 1"></span>
        </div>
        <div class="row address clearfix">
          <div class="content clearfix">
            <span class="xing">*</span>
            <span class="name"></span>
          </div>
          <!--  s -->
          <input class="input-address"
                 maxlength="26"
                 placeholder="4-26"
                 v-model="address"
                 v-bind:class="{border: noticeStatus2 == 2}">
          <!--  e -->
          <!--  s -->
          <span class="right-icon" v-if="noticeStatus2 == 1"></span>
          <!--  e -->
          <!--  s -->
          <div class="notice-info" v-if="noticeStatus2 == 2">
            <span class="error-icon"></span>
            <span class="notice-text">{{ noticeContent2 }}</span>
          </div>
          <!--  e -->
        </div>
        <div class="row receiver clearfix">
          <div class="content clearfix">
            <span class="xing">*</span>
            <span class="name">&nbsp;&nbsp;&nbsp;</span>
          </div>
          <!--  s -->
          <input class="input-receiver"
                 maxlength="10"
                 placeholder="2-10"
                 v-model="receiver"
                v-bind:class="{border: noticeStatus3 == 2}">
          <!--  e -->
          <!--  s -->
          <span class="right-icon" v-if="noticeStatus3 == 1"></span>
          <!--  e -->
          <!--  s -->
          <div class="notice-info" v-if="noticeStatus3 == 2">
            <span class="error-icon"></span>
            <span class="notice-text">{{ noticeContent3 }}</span>
          </div>
          <!--  e -->
        </div>
        <div class="row phone clearfix">
          <div class="content clearfix">
            <span class="xing">*</span>
            <span class="name">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
          </div>
          <!--  s -->
          <input maxlength="11"
                 class="input-phone"
                 placeholder="13452525625"
                 v-model="phoneNum"
                  v-bind:class="{border: noticeStatus4 == 2}">
          <!--  e -->
          <!--  s -->
          <span class="right-icon" v-if="noticeStatus4 == 1"></span>
          <!--  e -->
          <!--  s -->
          <div class="notice-info" v-if="noticeStatus4 == 2">
            <span class="error-icon"></span>
            <span class="notice-text">{{ noticeContent4 }}</span>
          </div>
          <!--  e -->
        </div>
        <div class="row tel-phone clearfix">
          <div class="content clearfix">
            <span class="xing">*</span>
            <span class="name"></span>
          </div>
          <input class="input-tel"
                 placeholder="78()"
                 maxlength="8"
                 v-model="telNum"
                v-bind:class="{border: noticeStatus5 == 2}">
          <span class="right-icon" v-if="noticeStatus5 == 1"></span>
          <div class="notice-info" v-if="noticeStatus5 == 2">
            <span class="error-icon"></span>
            <span class="notice-text">{{ noticeContent5 }}</span>
          </div>
        </div>
        <div class="row clearfix">
          <input type="button" class="button-submit" value="" @click.stop="submitReceiveInfo()">
          <p class="info-text"></p>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
  import {mapState, mapActions} from 'vuex';
  import selectAddress from '../components/selectAddress';

  export default {

    name: 'receiveInfo',

    components: {
      selectAddress
    },

    data() {
      return {
        //
        address: null,

        //
        receiver: null,

        //
        phoneNum: '',

        //
        telNum: '',

        //012
        noticeStatus1: 0,
        noticeStatus2: 0,
        noticeStatus3: 0,
        noticeStatus4: 0,
        noticeStatus5: 0,

        //
        noticeContent1: null,
        noticeContent2: null,
        noticeContent3: null,
        noticeContent4: null,
        noticeContent5: null,

        isShowResult: false
      };
    },

    //
    mounted: function () {
      this.$nextTick(() => {
        document.title = '';
        document.body.style.backgroundColor = '#fff';

        //selectAddress
        this.bus.$on('select-address', () => {
          this.noticeStatus1 = 0;
        });
      });
    },

    computed: mapState([
      'isReceive'
    ]),

    methods: {

      ...mapActions([
        'changeReceiveStatus'
      ]),

      //
      submitReceiveInfo: function () {

        this.noticeStatus1 = 1;

        //
        if (!this.checkAddress()) {
          return;
        }

        //
        if (!this.checkReceiver()) {
          return;
        }

        //
        if (!this.checkPhoneNum()) {
          return;
        }

        //
        if (!this.checkTelNumber()) {
          return;
        }

        this.isShowResult = true;
        this.changeReceiveStatus(true);

        setTimeout(() => {
          this.isShowResult = false;
        }, 3000);
      },

      //
      recoverStatus: function () {

        if (this.checkAddress() !== 1) {
          this.noticeStatus2 = 0;
          this.noticeContent2 = '';
        }

        if (this.checkReceiver() !== 1) {
          this.noticeStatus3 = 0;
          this.noticeContent3 = '';
        }

        if (this.checkPhoneNum() !== 1) {
          this.noticeStatus4 = 0;
          this.noticeContent4 = '';
        }

        if (this.checkTelNumber() !== 1) {
          this.noticeStatus5 = 0;
          this.noticeContent5 = '';
        }
      },

      //
      checkAddress: function () {

        //
        if (this.address === null || this.address === '') {
          this.noticeStatus2 = 2;
          this.noticeContent2 = '';
          return 0;
        }

        //
        if (this.address.length < 4 || this.address.length > 26) {
          this.noticeStatus2 = 2;
          this.noticeContent2 = '4~26';
          return 0;
        }

        this.noticeStatus2 = 1;
        this.noticeContent2 = '';
        return 1;
      },

      //
      checkReceiver: function () {

        //
        if (this.receiver === null || this.receiver === '') {
          this.noticeStatus3 = 2;
          this.noticeContent3 = '';
          return 0;
        }

        //
        if (this.receiver.length < 2 || this.receiver.length > 10) {
          this.noticeStatus3 = 2;
          this.noticeContent3 = '2~10';
          return 0;
        }

        this.noticeStatus3 = 1;
        this.noticeContent3 = '';
        return 1;
      },

      //
      checkPhoneNum: function () {

        //
        if (this.phoneNum === null || this.phoneNum === '') {
          this.noticeStatus4 = 2;
          this.noticeContent4 = '';
          return 0;
        }

        if (!(/^1(3[0-9]|4[57]|5[0-35-9]|7[0135678]|8[0-9])\d{8}$/.test(this.phoneNum))) {
          this.noticeStatus4 = 2;
          this.noticeContent4 = '';
          return 0;
        }

        this.noticeStatus4 = 1;
        this.noticeContent4 = '';
        return 1;
      },

      //
      checkTelNumber: function () {

        if (this.telNum === null || this.telNum === '') {
          this.noticeStatus5 = 0;
          this.noticeContent5 = '';
          return 1;
        }

        if (this.telNum.length < 7 || isNaN(this.telNum)) {
          this.noticeStatus5 = 2;
          this.noticeContent5 = '';
          return 0;
        }

        this.noticeStatus5 = 1;
        this.noticeContent5 = '';
        return 1;
      }
    }
  };
</script>

<style scoped>
  /*  */
  .receive-info {
    border: solid 1px #CCCCCC;
  }

  .receive-info .title {
    height: 30px;
    line-height: 30px;
    padding-left: 10px;
    background-color: #F2F2F2;
    border-top: solid 1px #fff;
    border-bottom: solid 1px #E3E3E3;
    font-size: 14px;
    color: #444444;
  }

  .receive-content {
    padding-top: 5px;
    padding-left: 30px;
  }

  .row {
    margin-bottom: 16px;
    position: relative;
  }

  .row .content {
    position: relative;
    top: 2px;
    float: left;
  }

  /*  */
  .row .xing {
    margin-top: 3px;
    margin-right: 8px;
    font-size: 14px;
    color: #FF0000;
    float: left;
  }

  .row .name {
    margin-right: 5px;
    font-size: 14px;
    color: #444444;
    float: left;
  }

  /*  */
  .input-address {
    width: 373px;
    height: 22px;
    line-height: 22px;
    padding-left: 10px;
    border: solid 1px #C2C2C2;
  }

  /*  */
  .right-icon {
    width: 16px;
    height: 16px;
    background: url("../../static/images/icon/warning-icon.png") no-repeat;
    background-position: 0px -117px;
    transform: translateY(-50%);
    position: absolute;
    left: 476px;
    top: 50%;
  }

  /*  */
  .error-icon {
    width: 18px;
    height: 16px;
    background: url("../../static/images/icon/warning-icon.png") no-repeat;
    background-position: -17px -100px;
    float: left;
  }

  .notice-info {
    height: 16px;
    line-height: 16px;
    color: #e22;
    transform: translateY(-50%);
    position: absolute;
    left: 476px;
    top: 50%;
  }

  .notice-info .notice-text {
    float: left;
  }

  /*  */
  .input-receiver {
    width: 373px;
    height: 22px;
    line-height: 22px;
    padding-left: 10px;
    border: solid 1px #C2C2C2;
    position: relative;
    left: 2px;
  }

  /*  */
  .input-phone {
    width: 373px;
    height: 22px;
    line-height: 22px;
    padding-left: 10px;
    border: solid 1px #C2C2C2;
    position: relative;
    left: 2px;
  }

  .row i {
    display: inline-block;
    height: 22px;
    line-height: 22px;
    margin-left: 10px;
    font-style: normal;
    font-size: 14px;
  }

  /*  */
  .input-tel {
    width: 373px;
    height: 22px;
    line-height: 22px;
    padding-left: 10px;
    border: solid 1px #C2C2C2;
    color: #000;
    position: relative;
    left: 1px;
  }

  /*  */
  .button-submit {
    width: 112px;
    height: 27px;
    line-height: 27px;
    text-align: center;
    background-color: #4492FC;
    border: solid 1px #3784ED;
    border-bottom: none;
    color: #fff;
    cursor: pointer;
    float: left;
  }

  .info-text {
    height: 27px;
    line-height: 27px;
    padding-left: 10px;
    font-size: 16px;
    color: #e4393c;
    display: none;
    float: left;
  }

  .show-result {
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    background-color: #ff6d28;
    position: relative;
  }

  .border {
    border-color: rgb(238, 34, 34);
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }
</style>



