
<!--  -->

<template>
  <a class="go-top" href="javascript:;" v-if="isShow" @click="goTop()"></a>
</template>

<script>
  export default {
    name: 'goTop',

    data() {
      return {
        leader: 0,
        target: 0,
        timer: null,
        isShow: false
      };
    },

    //
    mounted: function () {
      this.$nextTick(() => {

        //
        window.onscroll = () => {

          //
          var scrollTop = this.getScrollTop();

          //
          this.isShow = scrollTop > 0;

          //
          this.leader = scrollTop;
        };
      });
    },

    methods: {

      //
      goTop: function () {

        //
        clearInterval(this.timer);

        this.target = 0;

        this.timer = setInterval(() => {

          this.leader = this.leader + (this.target - this.leader) / 10;

          //
          window.scrollTo(0, this.leader);

          if (this.leader === this.target) {
            clearInterval(this.timer);
          }
        }, 10);
      },

      //
      getScrollTop: function () {

        var scrollPos;

        if (window.pageYOffset) {

          scrollPos = window.pageYOffset;

        } else if (document.compatMode && document.compatMode !== 'BackCompat') {

          scrollPos = document.documentElement.scrollTop;

        } else if (document.body) {

          scrollPos = document.body.scrollTop;
        }

        return scrollPos;
      }
    }
  };
</script>

<style scoped>
  .go-top {
    width: 68px;
    height: 29px;
    background: url(../../static/images/top.png) no-repeat left bottom;
    cursor: pointer;
    position: fixed;
    right: 15px;
    bottom: 75px;
  }

  .go-top:hover {
    background-position: left top;
  }
</style>



