
import Vue from 'vue';
import Vuex from 'vuex';
import actions from './action';
import mutations from './mutations';

//Vuex
Vue.use(Vuex);

const state = {

  //
  isLogin: false,

  //
  isShowMask: false,

  //
  username: null,

  //
  productNum: 0,

  //
  productPrice: 0,

  //
  currentNavIndex: 1,

  //
  brand: null,

  //
  screenSize: null,

  //
  operatingSystem: null,

  //
  runningMemory: null,

  //
  networkType: null,

  //
  isReceive: false,

  //0:1:2:
  payMethod: 0,

  //
  isInvoice: false,

  //0:1:,2:
  checkStatus: 0,

  //
  checkContent: null
};

export default new Vuex.Store({
  state,
  actions,
  mutations
});



