
<!--  -->

<template>
  <nav class="nav-bar">

    <!--  s -->
    <div class="inner-c clearfix">

      <!--  s -->
      <div class="product-cate fl">
        <h3></h3>
      </div>
      <!--  e -->

      <!--  -->
      <ul class="nav fl clearfix">
        <li v-bind:class="{active: currentNavIndex == 1}" @click="changeNavIndex(1)">
          <router-link to="/cate"></router-link>
        </li>
        <li v-bind:class="{active: currentNavIndex == 2}" @click="changeNavIndex(2)">
          <router-link to="/cate"></router-link>
        </li>
        <li v-bind:class="{active: currentNavIndex == 3}" @click="changeNavIndex(3)">
          <router-link to="/cate"></router-link>
        </li>
        <li v-bind:class="{active: currentNavIndex == 4}" @click="changeNavIndex(4)">
          <router-link to="/cate"></router-link>
        </li>
        <li v-bind:class="{active: currentNavIndex == 5}" @click="changeNavIndex(5)">
          <router-link to="/cate"></router-link>
        </li>
        <li v-bind:class="{active: currentNavIndex == 6}" @click="changeNavIndex(6)">
          <router-link to="/cate"></router-link>
        </li>
      </ul>
      <!--  s -->

    </div>
    <!--  e -->

  </nav>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {

    //
    name: 'nav-bar',

    computed: mapState([
      //
      // this.currentNavIndex  store.state.currentNavIndex
      'currentNavIndex'
    ]),

    methods: {

      //
      ...mapActions([

        //
        'changeNavIndex'
      ])
    }
  };
</script>

<style scoped>
  .nav-bar {
    width: 100%;
    height: 35px;
    background-color: #1369c0;
    display: table;
  }

  .product-cate {
    float: left;
  }

  .product-cate h3 {
    width: 149px;
    height: 35px;
    padding-left: 40px;
    color: rgb(255, 255, 255);
    background: url(../../static/images/icon/arrow-down.png) 148px center no-repeat;
    font: 16px/35px , "microsoft yahei";
  }

  .nav {
    float: left;
  }

  .nav li {
    width: 100px;
    height: 35px;
    font: 14px/35px , "microsoft yahei";
    text-align: center;
    cursor: pointer;
    float: left;
  }

  .nav li:hover,
  .nav li.active {
    background-color: #4593fd;
  }

  .nav li a {
    width: 100px;
    height: 35px;
    color: #fff;
    display: block;
  }
</style>



