
<!--  -->

<template>
  <div class="top-bar">
    <div class="inner-c clearfix">
      <div class="left-area">
        <a href="javascript:;" class="collection" @click="showMask()"></a>
      </div>
      <div class="right-area">
        <!--  s -->
        <div v-if="isLogin">
          <a class="show-username" href="javascript:;">{{ username }}</a>
          <a class="exit" href="javascript:;" @click="exitAccount()">[]</a>
        </div>
        <!--  e -->
        <!--  s-->
        <div v-else>
          
          <router-link class="login-link" to="/login">[]</router-link>
          <router-link class="register-link" to="/register">[]</router-link>
        </div>
        <!--  e -->
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';

  export default {

    //
    name: 'topBar',

    computed: mapState([

      //
      'username',

      //
      'isLogin'
    ]),

    methods: {
      //
      ...mapActions([

        //
        'showMask',

        //
        'exit',

        //
        'setUsername',

        'changeLoginStatus'
      ]),

      //
      exitAccount: function () {
        this.exit();
        this.$router.push('/');
      }
    }
  };
</script>

<style scoped>
  .top-bar {
    width: 100%;
    height: 38px;
    font: 12px/38px "", 'SimSun';
    background-color: #f7f7f7;
    color: #666;
  }

  .top-bar .left-area {
    float: left;
  }

  .top-bar .left-area .collection {
    padding-left: 17px;
    background: url(../../static/images/icon/collection.jpg) left center no-repeat;
    font-size: 12px;
    font-weight: bold;
  }

  .right-area {
    float: right;
  }

  .right-area .show-username {
    color: #C81623;
  }
</style>



