
<!--  -->

<template>
  <div class="register">
    <registerHeader></registerHeader>
    <!--  s -->
    <div class="show-result" v-if="resultStatus == 1">
      
      <span class="time">{{ numTime }}</span>
      
    </div>
    <!--  e -->
    <!--  s -->
    <div class="show-result" v-if="resultStatus == 0 || resultStatus === 2">
      <span class="text">{{ resultContent }}</span>
      <button class="close-btn" @click="resultStatus = -1">
        <i class="fa fa-times"></i>
      </button>
    </div>
    <!-- -->
    <section class="register-box">
      <form @click.stop="recoverStatus()">
        <ul class="register">
          <!--  s -->
          <li class="username">
            <div class="item-content clearfix">
              <div class="register-item">
                <i>*</i>
                
              </div>
              <div class="input-item">
                <input class="icon-username input-username"
                       spellcheck="false"
                       v-model="username"
                       v-bind:class="{error: usernameStatus == 2}"
                      @click="usernameStatus = 0">
              </div>
            </div>
            <!--  s -->
            <div class="tips" v-if="usernameStatus == 0">3~15</div>
            <!--  e -->
            <!--  s -->
            <div class="notice-info" v-if="usernameStatus != 0" v-bind:class="{success: usernameStatus == 1}">
              <span class="notice-icon"></span>
              <span class="notice-content">{{ usernameNotice }}</span>
            </div>
            <!--  e -->
          </li>
          <!--  e -->
          <!--  s -->
          <li class="password">
            <div class="item-content clearfix">
              <div class="register-item">
                <i>*</i>
                
              </div>
              <div class="input-item">
                <input class="icon-password input-password"
                       type="password"
                        v-model="password"
                        v-bind:class="{error: passwordStatus == 2}"
                        @click="passwordStatus = 0">
              </div>
            </div>
            <div class="tips" v-if="passwordStatus == 0">6~16</div>
            <div class="notice-info" v-if="passwordStatus != 0" v-bind:class="{success: passwordStatus == 1}">
              <span class="notice-icon"></span>
              <span class="notice-content">{{ passwordNotice }}</span>
            </div>
          </li>
          <!--  e -->
          <!--  s -->
          <li class="second-password">
            <div class="item-content clearfix">
              <div class="register-item">
                <i>*</i>
                
              </div>
              <div class="input-item">
                <input class="icon-password input-second-password" type="password"
                        v-model="secondPassword"
                        v-bind:class="{error: secondPasswordStatus == 2}"
                        @click="secondPasswordStatus = 0">
              </div>
            </div>
            <div class="tips" v-if="secondPasswordStatus == 0"></div>
            <div class="notice-info" v-if="secondPasswordStatus != 0" v-bind:class="{success: secondPasswordStatus == 1}">
              <span class="notice-icon"></span>
              <span class="notice-content">{{ secondPasswordNotice }}</span>
            </div>
          </li>
          <!--  e -->
          <!--  s -->
          <li class="email">
            <div class="item-content clearfix">
              <div class="register-item">
                <i>*</i>
                
              </div>
              <div class="input-item">
                <input class="input-email"
                       spellcheck="false"
                        v-model="email"
                        v-bind:class="{error: emailStatus == 2}"
                        @click="emailStatus = 0">
              </div>
            </div>
            <div class="tips" v-if="emailStatus == 0"></div>
            <div class="notice-info" v-if="emailStatus != 0" v-bind:class="{success: emailStatus == 1}">
              <span class="notice-icon"></span>
              <span class="notice-content">{{ emailNotice }}</span>
            </div>
          </li>
          <!--  e -->
          <!--  s -->
          <li class="clause">
            <div class="item-content clearfix">
              <div class="checkbox-item">
                <input type="checkbox"
                       id="agree"
                        v-model="isAccept"
                        @click="acceptStatus = 0">
                <label for="agree"></label>
                <a href="javascript:;"></a>
              </div>
              <div class="notice-info" v-if="acceptStatus == 2">
                <span class="notice-icon"></span>
                <span class="notice-content">{{ acceptNotice }}</span>
              </div>
            </div>
          </li>
          <!--  e -->
          <!--  s -->
          <li class="submit">
            <div class="item-content">
              <input type="button" value="" class="buttonRegister" @click.stop="registerAccount()">
            </div>
          </li>
          <!--  e -->
        </ul>
      </form>
    </section>
    <div class="hr-40"></div>
    <foot></foot>
  </div>
</template>

<script>
  import registerHeader from '../components/registerHeader';
  import foot from '../components/foot';
  import Util from '../js/Util';

  export default {
    name: 'register',

    components: {
      registerHeader,
      foot
    },

    mounted: function () {
      this.$nextTick(() => {
        document.title = '';
        document.body.style.backgroundColor = '#fff';
        window.scrollTo(0, 0);
      });
    },

    data() {
      return {

        //
        username: null,

        //
        password: null,

        //
        secondPassword: null,

        //
        email: null,

        //
        isAccept: false,

        //0: 1: 2
        usernameStatus: 0,

        //0: 1: 2:
        passwordStatus: 0,

        //0: 1: 2:
        secondPasswordStatus: 0,

        //0: 1: 2:
        emailStatus: 0,

        //0: 1: 2:
        acceptStatus: 0,

        //
        usernameNotice: null,

        //
        passwordNotice: null,

        //
        secondPasswordNotice: null,

        //
        emailNotice: null,

        //
        acceptNotice: null,

        resultContent: null,

        // -1: 0: 1: 2:
        resultStatus: -1,

        numTime: 3
      };
    },

    methods: {

      //
      registerAccount: function () {

        this.usernameStatus = Util.checkUsername(this.username).usernameStatus;
        this.usernameNotice = Util.checkUsername(this.username).usernameNotice;

        //
        if (!Util.checkUsername(this.username).flag) {
          return;
        }

        this.passwordStatus = Util.checkPassword(this.password).passwordStatus;
        this.passwordNotice = Util.checkPassword(this.password).passwordNotice;

        //
        if (!Util.checkPassword(this.password).flag) {
          return;
        }

        this.secondPasswordStatus = Util.checkSecondPassword(this.password, this.secondPassword).secondPasswordStatus;
        this.secondPasswordNotice = Util.checkSecondPassword(this.password, this.secondPassword).secondPasswordNotice;

        //
        if (!Util.checkSecondPassword(this.password, this.secondPassword).flag) {
          return;
        }

        this.emailStatus = Util.checkEmail(this.email).emailStatus;
        this.emailNotice = Util.checkEmail(this.email).emailNotice;

        //
        if (!Util.checkEmail(this.email).flag) {
          return;
        }

        this.acceptStatus = Util.checkAccept(this.isAccept).acceptStatus;
        this.acceptNotice = Util.checkAccept(this.isAccept).acceptNotice;

        //
        if (!Util.checkAccept(this.isAccept).flag) {
          return;
        }

        //axiospost
        this.axios({
          method: 'post',
          url: this.registerUrl,
          data: this.qs.stringify({
            username: this.username,
            password: this.password,
            email: this.email
          }),
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then(res => {

          //
          if (res.data === 1) {
            this.resultStatus = 1;
            let timer = setInterval(() => {

              this.numTime--;

              if (this.numTime <= 0) {
                clearInterval(timer);
                this.$router.push('/login');
              }
            }, 1000);

          //
          } else if (res.data === 0) {
            this.resultStatus = 0;
            this.resultContent = '';

          //
          } else if (res.data === 2) {
            this.resultStatus = 2;
            this.resultContent = '';
          }
        });
      },

      //
      recoverStatus: function () {

        if (this.usernameStatus !== 1) {
          this.usernameStatus = 0;
          this.usernameNotice = '';
        }

        if (this.passwordStatus !== 1) {
          this.passwordStatus = 0;
          this.passwordNotice = '';
        }

        if (this.secondPasswordStatus !== 1) {
          this.secondPasswordStatus = 0;
          this.secondPasswordNotice = '';
        }

        if (this.emailStatus !== 1) {
          this.emailStatus = 0;
          this.emailNotice = '';
        }

        this.acceptStatus = 0;
      }
    }
  };
</script>

<style scoped>
  .register-box {
    width: 628px;
    padding-top: 10px;
    margin: 10px auto;
    border: solid 1px #ccc;
  }

  .register li {
    height: 70px;
    vertical-align: top;
  }

  .register li:nth-of-type(5) {
    height: 38px;
  }

  .register-item {
    width: 190px;
    height: 38px;
    font: 14px/38px "", "SimSun";
    color: #999999;
    text-align: right;
    float: left;
  }

  .register-item i {
    color: #FF0000;
    font-style: normal;
  }

  .input-item {
    float: left;
  }

  .input-item input {
    width: 263px;
    height: 36px;
    line-height: 36px;
    padding-left: 5px;
    font-size: 16px;
    border: solid 1px #ccc;
  }

  .icon-username {
    background: url("../../static/images/icon/user.jpg") 245px center no-repeat;
  }

  .icon-password {
    background: url("../../static/images/icon/password.jpg") 245px center no-repeat;
  }

  .checkbox-item {
    margin-left: 190px;
  }

  .checkbox-item input {
    position: relative;
    top: 2px;
  }

  .checkbox-item label {
    color: #333333;
  }

  .checkbox-item a {
    color: #005AA0;
  }

  .submit-item {
    margin-left: 190px;
  }

  .submit input {
    width: 263px;
    height: 52px;
    margin-left: 190px;
    margin-top: 10px;
    font: 22px/52px "";
    color: #fff;
    text-align: center;
    background: #69b946;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    border-color: transparent;
    cursor: pointer;
    display: inline-block;
  }

  .tips {
    height: 14px;
    line-height: 14px;
    margin-left: 190px;
    padding: 2px 0 6px;
    color: #999;
  }

  .notice-info {
    margin-top: 3px;
    margin-left: 190px;
    color: #e22;
  }

  .notice-info .notice-icon {
    width: 16px;
    height: 16px;
    vertical-align: top;
    background: url(../../static/images/icon/warning-icon.png) -17px -100px no-repeat;
    display: inline-block;
  }

  .item-content .error {
    border-color: #EE2222;
  }

  .success .notice-icon {
    background-position: 0px -117px;
  }

  .success .notice-content {
    color: #3D882D;
  }

  .show-result {
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    background-color: #ff6d28;
    position: relative;
  }

  .show-result .close-btn {
    width: 26px;
    height: 26px;
    line-height: 24px;
    margin-left: 274px;
    background: #f86621;
    border-radius: 2px;
    vertical-align: text-bottom;
    font-size: 18px;
    color: #ffcbb3;
    border: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    outline: none;
    cursor: pointer;
    position: absolute;
    right: 30px;
    top: 7px;
  }

  .show-result .time {
    color: #000;
  }
</style>



