
<!--  -->

<template>
  <div class="product-cate">
    <h3 class="title">{{ productCateData.title }}</h3>
    <dl class="cate-item" v-for="cate in productCateData.cateList">
      <dt>{{ cate.cateTitle }}</dt>
      <dd>
        <ul>
          <li v-for="content in cate.cateContent">
            <router-link to="/proinfo">{{ content }}</router-link>
          </li>
        </ul>
      </dd>
    </dl>
  </div>
</template>

<script>
  export default {
    name: 'productCate',

    data() {
      return {
        //
        productCateData: {
          "title": '',
          "cateList": [
            {
              "cateTitle": '',
              "cateContent": ['', '']
            },
            {
              "cateTitle": '',
              "cateContent": ['', '0', '']
            },
            {
              "cateTitle": '',
              "cateContent": ['', '', '/', '', '',
              '', '', '', 'iPhone']
            }
          ]
        }
      };
    }
  };
</script>

<style scoped>
  .product-cate .title {
    height: 34px;
    text-align: center;
    color: #000;
    font: bold 16px/34px "", "microsoft yahei";
    border-bottom: solid 1px #d7d7d7;
  }

  .product-cate .cate-item {
    padding: 4px 0 0 10px;
    border-bottom: dotted 1px #d7d7d7;
  }

  .product-cate .cate-item:nth-last-child(1) {
    border-bottom: solid 1px #d7d7d7;
  }

  .product-cate .cate-item dt {
    font: bold 14px/24px "", "microsoft yahei";
  }

  .product-cate .cate-item li {
    height: 22px;
    padding-left: 12px;
    font-size: 12px;
    font-family: "";
  }

  .product-cate .cate-item li a {
    color: #555;
  }

  .product-cate .cate-item li a:hover {
    color: #c81623;;
  }
</style>



